.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

section {
  min-height: 100vh;

}

#title {
  position: sticky;
  top: 0;
  padding: 15px;
  font-size: x-large;
  background-color: hotpink;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.floating {
  position: sticky;
  z-index: 10;
  top: 100%;
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.radioGroup {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  margin-top: 20px;
  border-radius: 8px;
  background-color: hotpink;
  padding: 30px;
  box-shadow: 0px 2px 3.84px rgba(0, 0, 0, 0.25);
}

input[type=radio] {
  appearance: none;
}

input[type=radio]+label {
  padding: 10px;
  border-radius: 30px;
}

input[type=radio]:checked+label {
  background-color: #61dafb;
}